<template>
  <fieldset class="data">
    <legend class="title">–ü–∞—Å–ø–æ—Ä—Ç–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ:</legend>
    <select class="select" v-model.lazy="$v.documentType.$model">
      <optgroup label="–¢–∏–ø –¥–æ–∫—É–º–µ–Ω—Ç–∞*">
        <option value="">–í—ã–±–µ—Ä–∏—Ç–µ —Ç–∏–ø –¥–æ–∫—É–º–µ–Ω—Ç–∞:</option>
        <option value="passport">–ü–∞—Å–ø–æ—Ä—Ç</option>
        <option value="certificate">–°–≤–∏–¥–µ—Ç–µ–ª—å—Å—Ç–≤–æ –æ —Ä–æ–∂–¥–µ–Ω–∏–∏</option>
        <option value="license">–í–æ–¥. —É–¥–æ—Å—Ç–æ–≤–µ—Ä–µ–Ω–∏–µ</option>
      </optgroup>
    </select>
    <span class="invalid" v-if="$v.documentType.$error">
      –ü–æ–ª–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è!
    </span>
    <input type="text" class="input" placeholder="–°–µ—Ä–∏—è">
    <input
      type="text"
      class="input"
      placeholder="–ù–æ–º–µ—Ä*"
      v-model.lazy="$v.number.$model"
    >
    <span class="invalid" v-if="$v.number.$error">
      –ù–æ–º–µ—Ä –ø–∞—Å–ø–æ—Ä—Ç–∞ –¥–æ–ª–∂–µ–Ω –±—ã—Ç—å –≤ —Ñ–æ—Ä–º–∞—Ç–µ 567890!
    </span>
    <input type="text" class="input" placeholder="–ö–µ–º –≤—ã–¥–∞–Ω">
    <input
      type="text"
      class="input"
      placeholder="–î–∞—Ç–∞ –≤—ã–¥–∞—á–∏*"
      v-model.lazy="$v.date.$model"
    >
    <span class="invalid" v-if="$v.date.$error">
      –î–∞—Ç–∞ –¥–æ–ª–∂–Ω–∞ –±—ã—Ç—å –≤ —Ñ–æ—Ä–º–∞—Ç–µ –î–î.–ú–ú.–ì–ì–ì–ì!
    </span>
  </fieldset>
</template>

<script>
import { required } from 'vuelidate/lib/validators'

export default {
  data () {
    return {
      documentType: '',
      number: '',
      date: ''
    }
  },
  validations: {
    documentType: { required },
    number: {
      required,
      validFormat: value => /^\d{6}$/.test(value)
    },
    date: {
      required,
      validDate: value => /(0?[1-9]|[12][0-9]|3[01]).(0?[1-9]|1[012]).((19|20)\d\d)/.test(value)
    }
  },
  updated () {
    this.$emit('changeValid', this.$v.$invalid, 'passport')
  }
}
</script>
